
.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}


.voda-ballot {

  text-align: center;

}


.fill-voda-ballot {

    text-align: center;

}

.participants {


       margin-right: 20%;
}


h1 {

    color: #33ffcc;

}


ul {
    list-style: none;
}


li {
    margin-bottom: 10px;
    text-align: center;

}

label {
    margin-right: 10px;
}

button {
    margin-right: 10px;
    margin-left: 10px;
}


#options input {

    vertical-align: bottom;
    padding: 5px;

}


.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
